.card {
    height: 300px;
    width: 260px;
    background: linear-gradient(145deg, #ffb6e5, #ff8ac6);
    padding: 20px;
    margin: 15px;
    border-radius: 20px;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
    transform: translateY(-10px);
    box-shadow: 0 20px 30px rgba(0, 0, 0, 0.2);
}

.card h3 {
    font-weight: 600;
    color: #333;
    margin-bottom: 10px;
}

.card p {
    font-weight: 400;
    color: #555;
    line-height: 1.5;
}


